@charset "UTF-8";
/* Default styles */
@import 'google-fonts.css';
:root {
  --yale-blue: hsla(212, 72%, 26%, 1);
  --berkeley-blue: hsla(215, 66%, 22%, 1);
  --oxford-blue: hsla(213, 72%, 16%, 1);
  --powder-blue: hsla(209, 32%, 66%, 1);
  --mint-cream: hsla(111, 24%, 94%, 1);
  --phthalo-blue: hsla(243, 76%, 27%, 1);
  --black-pearl: hsla(230, 70%, 5%, 1);
  --lavender-web: hsla(248, 54%, 89%, 1);
  --moonstone: hsla(185, 84%, 38%, 1);
  --moonstone-dark: hsl(200, 84%, 38%);
  --russian-violet: hsla(295, 100%, 15%, 1);
  --chinese-violet: hsla(269, 9%, 45%, 1);
  --btn-grey: hsla(240, 3%, 12%, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-weight: normal;
  color: var(--mint-cream);
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

body {
  color: var(--mint-cream);
  min-height: 800px;
  max-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  transition: background 0.25s ease-in-out;
  overflow-x: clip;
  max-width: 100vw;
  background: #000 url(../src/media/bg-004-1.webp) top center/100% 100vh no-repeat;
}
body::-webkit-scrollbar {
  width: 0.35em;
}
body::-webkit-scrollbar-track {
  background: var(--black-pearl);
  border-left: 1px solid hsla(111, 24%, 94%, 0.1);
}
body::-webkit-scrollbar-thumb {
  background: var(--lavender-web);
}

h1 {
  font-family: "Bunken Tech Sans SC Wide W01Bd";
  text-transform: uppercase;
}

body {
  font-family: "Montserrat", sans-serif;
}

a {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 2.489rem; /* 39.84px */
}

h2 {
  font-size: 2.074rem; /* 33.12px */
}

h3 {
  font-size: 1.728rem; /* 27.68px */
}

h4 {
  font-size: 1.44rem; /* 23.04px */
}

h5 {
  font-size: 1.2rem; /* 19.2px */
}

small {
  font-size: 0.833rem; /* 13.28px */
}

header {
  padding: 8px 1em 1em;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav a {
  text-transform: uppercase;
}

.logo {
  display: flex;
  height: max-content;
  z-index: 3;
}

.ico-planet {
  height: 45px;
  width: auto;
}
.ico-planet img {
  width: 100%;
  height: 100%;
}

.link {
  padding: 10px 0 0;
  z-index: 3;
}

.nav-links {
  display: flex;
  gap: 1.75em;
}

.command a {
  padding: 8px 12px;
  border-radius: 1.75em;
  border: 1px solid hsla(111, 24%, 94%, 0.75);
}

.burger-btn {
  display: none;
  position: absolute;
  right: 1em;
  width: 30px;
  height: 30px;
  top: 18px;
  z-index: 5;
  opacity: 0;
}

.burger-menu {
  display: none;
  width: max-content;
}
.burger-menu span {
  height: 4px;
  width: 24px;
  border-radius: 2px;
  background: var(--lavender-web);
}

#toggle-off {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  background: red;
  width: 100vw;
  height: 100vh;
  z-index: 2;
}

footer {
  max-width: 100vw;
  text-align: left;
  line-height: 25px;
  padding: 0 0 1em;
  margin-top: 6em;
  position: relative;
  font-family: "Poppins";
  background: hsla(0, 0%, 0%, 0.5);
  backdrop-filter: blur(2px);
  box-shadow: -4px 0px 5px #000, -4px 0px 20px #000, -6px 0px 30px #000, -6px 0px 15px #000 inset, 6px 0px 20px #000 inset;
}
footer a {
  font-size: 1rem;
}
@media (max-width: 1280px) {
  footer .deco.ship {
    top: -25px;
  }
  footer .deco.spatio {
    width: 250px;
  }
}
@media (max-width: 1024px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: initial;
    bottom: 0;
    width: 125px;
  }
  footer .deco.spatio {
    width: 150px;
  }
  footer .get-in-touch {
    max-width: 80%;
  }
  footer .copyright {
    font-weight: 200;
    font-size: 0.9rem;
  }
}
@media (max-width: 768px) {
  footer .get-in-touch {
    max-width: 90%;
  }
}
@media (max-width: 640px) {
  footer .get-in-touch  {
    flex-direction: column;
    text-align: center;
    gap: 1em;
  }
  footer .adress {
    order: 1;
  }
  footer .tel {
    order: 2;
  }
  footer .mail {
    order: 3;
  }
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 12px;
    width: 150px;
  }
  footer .deco.spatio {
    right: 24px;
    width: 150px;
  }
}
@media (max-width: 475px) {
  footer .deco {
    width: 250px;
  }
  footer .deco.ship {
    top: -10%;
    bottom: initial;
    left: 1em;
    width: 100px;
  }
  footer .deco.spatio {
    top: -15%;
    right: 1em;
    width: 100px;
  }
}

.separation {
  height: 2px;
  border-radius: 4px;
  width: 50%;
  min-width: 200px;
  margin: 0 auto calc(3em + 12px);
  background: var(--lavender-web);
  position: relative;
  top: -1px;
}

.get-in-touch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 60%;
  min-width: 280px;
  margin: 0 auto;
  text-transform: uppercase;
}
.get-in-touch a {
  font-weight: 200;
}
.get-in-touch ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 1em;
}
.get-in-touch li {
  width: max-content;
}
.get-in-touch .adress {
  pointer-events: none;
}

.social-media {
  display: flex;
  justify-content: center;
  gap: 1em;
  margin: 2em 0 1em;
}

.ico {
  transition: transform ease-in-out 0.25s;
  cursor: pointer;
}
.ico:hover {
  transform: scale(1.1) translateY(-5px);
}

.copyright {
  text-align: center;
}

.deco {
  position: absolute;
  top: -75px;
  height: auto;
  pointer-events: none;
  z-index: 1;
}
.deco img {
  width: 100%;
  height: 100%;
}
.deco.ship {
  left: clamp(5px, 10%, 50px);
  top: -25px;
  width: 200px;
  animation: flying 1.5s ease infinite;
}
.deco.spatio {
  right: clamp(5px, 10%, 50px);
  width: 200px;
  animation: floating 5s ease infinite;
}

.get-to-top {
  position: absolute;
  top: -115px;
  right: 1em;
  filter: invert(100%);
  opacity: 0.7;
}

#hero {
  min-height: calc(100vh - 65px);
  display: grid;
  align-items: center;
}
#hero .content {
  min-width: 300px;
  width: 50%;
}
#hero .content h1 {
  max-width: 15ch;
  margin: 0 0 8px;
}
#hero .content p {
  max-width: 50ch;
  line-height: 24px;
}

.c-t-a {
  display: flex;
  gap: 1.75em;
  margin: 2em 0 0;
}
.c-t-a span {
  border: 1px solid white;
  padding: 8px 12px;
  border-radius:   1.75em;
}

.bg-h {
  position: absolute;
  height: 70vh;
  right: clamp(1em, 10%, 45px);
}
.bg-h.spatio {
  right: 30%;
  filter: brightness(50%);
}
.bg-h.planet {
  height: 85vh;
  filter: brightness(35%);
}
.bg-h img {
  height: 100%;
  width: 100%;
}

#scroll {
  padding: 1em 0;
  position: relative;
}
#scroll h2 {
  text-align: center;
  margin: 0 0 1em;
}

.tools {
  display: flex;
  width: 100vw;
  height: fit-content;
  animation: slide 12s linear infinite;
}
.tools div {
  margin: 0 2em;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
[active-step] > div {
  display: none;
}

[active-step="1"] > .step-1,
[active-step="2"] > .step-2,
[active-step="3"] > .step-3,
[active-step="4"] > .step-4,
[active-step="5"] > .step-5 {
  display: block;
}

[type=radio],
[type=checkbox],
label {
  cursor: pointer;
}

.info {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
button {
  border: 2px solid var(--mint-cream);
  border-radius: 1.75em;
  background: var(--black-pearl);
  padding: 1em 1.75em;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  cursor: pointer;
}

.inputs {
  position: relative;
  width: max-content;
}
.inputs input:not([type=checkbox], [type=radio]) {
  color: var(--lavender-web);
  font-family: "League Spartan";
  font-weight: 300;
  font-size: 1rem;
  width: 40vmin;
}
.inputs input:not([type=checkbox], [type=radio]):focus + label {
  color: var(--moonstone);
}
.inputs input:not([type=checkbox], [type=radio]):placeholder-shown + label {
  top: 48%;
  transform: translateY(-50%);
  left: 34px;
  color: grey;
  padding: 0;
}
.inputs label {
  position: absolute;
  top: 4px;
  left: 2em;
  font-size: 0.95em;
  z-index: 2;
  color: var(--yale-blue);
  padding: 0 8px;
  transition: top 0.15s ease-in, left 0.15s ease-in, color 0.15s ease-in, border 0.15s ease-in, transform 0.15s ease-in, padding 0.15s ease-in;
  pointer-events: none;
}
.inputs label::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 4px;
  height: 4px;
  width: 95%;
  background: var(--black-pearl);
  z-index: -1;
  transition: background 0.15s ease-in;
}
@media (max-width: 475px) {
  .inputs label::after {
    top: 7px;
  }
}

input:not([type=checkbox], [type=radio]) {
  border: 2px solid var(--yale-blue);
  background: none;
  padding: 8px 16px;
  border-radius: 1.75em;
  margin: 1em;
  position: relative;
}
input:not([type=checkbox], [type=radio]):focus {
  outline: none;
  border-color: var(--moonstone);
}
@media (max-width: 475px) {
  input:not([type=checkbox], [type=radio]) {
    margin: 12px 1em;
  }
}

.check,
.option {
  line-height: 30px;
}
.check input,
.option input {
  opacity: 0;
}
.check input:checked + label::after,
.option input:checked + label::after {
  background: var(--black-pearl) url(/media/nav/check.svg) center/14px no-repeat;
  left: -5px;
}
.check input:checked + label::before,
.option input:checked + label::before {
  background: var(--black-pearl);
}
.check label,
.option label {
  padding-left: 1.5em;
  cursor: pointer;
  position: relative;
}
.check label::after,
.option label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.8);
  border-radius: 50%;
  border: 2px solid var(--yale-blue);
  left: -20px;
  width: 20px;
  aspect-ratio: 1/1;
  background: var(--mint-cream) url(/media/nav/check.svg) center/0px no-repeat;
  transition: 0.25s all ease-in-out;
}
.check label::before,
.option label::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.7);
  border: 2px solid var(--yale-blue);
  left: -20px;
  width: 34px;
  height: 20px;
  border-radius: 10px;
  background: var(--mint-cream);
  opacity: 0.7;
  transition: 0.25s all ease-in-out;
}

#order {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 86px);
}
#order h1 {
  text-align: center;
  margin-bottom: 1em;
}
@media (max-width: 1280px) {
  #order form {
    width: 90%;
  }
}
@media (max-width: 1024px) {
  #order .inputs input:not([type=checkbox], [type=radio]) {
    width: 30vmin;
  }
  #order .inputs input:not([type=checkbox], [type=radio]):focus + label {
    color: var(--moonstone);
  }
  #order .inputs input:not([type=checkbox], [type=radio]):placeholder-shown + label {
    top: 50%;
    transform: translateY(-50%);
    left: 34px;
    color: grey;
    padding: 0;
    background: none;
  }
}
@media (max-width: 768px) {
  #order {
    margin-top: 85px;
  }
  #order form {
    width: 100%;
  }
  #order .inputs input:not([type=checkbox], [type=radio]) {
    width: 90%;
  }
  #order .inputs input:not([type=checkbox], [type=radio]):focus + label {
    color: var(--moonstone);
  }
  #order .inputs input:not([type=checkbox], [type=radio]):placeholder-shown + label {
    top: 50%;
    transform: translateY(-50%);
    left: 34px;
    color: grey;
    padding: 0;
    background: none;
  }
}
@media (max-width: 640px) {
  #order form {
    width: 90%;
  }
  #order h2 {
    margin-bottom: 1em;
    font-size: 1rem;
  }
  #order .step-2 {
    height: max-content;
    margin: 1em 0;
  }
  #order .step-2 .info {
    height: max-content;
    margin: auto 0;
  }
  #order .step-2 .site-desc {
    gap: 1em;
    width: 90%;
  }
  #order .step-2 .site-desc .inputs input {
    margin: 1em 0;
    width: 40vmin;
  }
  #order .step-2 .site-desc .inputs input:placeholder-shown + label {
    top: 47%;
    left: 18px;
  }
  #order .step-2 .site-desc .inputs label {
    left: 16px;
  }
  #order .step-2 .site-desc .inputs label::after {
    top: 12px;
  }
  #order .step-2 .site-desc .last {
    grid-column: span 2;
  }
  #order .step-3 h2 {
    margin-bottom: 1em;
  }
  #order .step-3 .more {
    grid-template-columns: repeat(auto-fill, 140px);
    gap: 1.75em;
  }
  #order .step-5 {
    padding: 1em;
  }
  #order .step-5 h2 {
    margin-bottom: 8px;
  }
  #order .step-5 .radio {
    display: grid;
    grid-template-columns: 1fr;
    gap: 8px 0;
    margin: 0 auto 1em;
  }
  #order .step-5 .radio > div {
    display: flex;
    gap: 4px;
    justify-content: center;
  }
  #order .step-5 .radio input {
    opacity: 0;
    position: absolute;
  }
  #order .step-5 .radio .s-m {
    order: 3;
  }
  #order .step-5 label {
    max-width: 90%;
  }
}
@media (max-width: 475px) {
  #order {
    min-height: 80vh;
    margin-bottom: 20%;
  }
  #order form {
    padding: 1em 0 2.5em;
  }
  #order .inputs input:not([type=checkbox], [type=radio]) {
    width: 75vmin;
  }
  #order .inputs input:not([type=checkbox], [type=radio]):focus + label {
    color: var(--moonstone);
  }
  #order .step-2 {
    height: max-content;
    margin: 0;
    padding: 0 1em;
  }
  #order .step-2 .info {
    height: max-content;
    margin: auto 0;
  }
  #order .step-2 h2 {
    margin-bottom: 1em;
    width: 70%;
    margin: 0 auto;
  }
  #order .step-2 .check {
    margin: 0;
  }
  #order .step-2 .site-desc {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    margin: 0 auto;
  }
  #order .step-2 .site-desc .inputs {
    grid-column: span 2;
    display: flex;
    width: 100%;
  }
  #order .step-2 .site-desc .inputs input {
    justify-self: center;
    margin: 1em 0 0;
    width: 100%;
  }
  #order .step-2 .site-desc .inputs input:placeholder-shown + label {
    top: 62%;
    left: 18px;
  }
  #order .step-2 .site-desc .inputs label {
    left: 22px;
  }
  #order .step-2 .site-desc .inputs label::after {
    top: 12px;
  }
  #order .step-2 .site-desc .option {
    grid-column: span 2;
    margin: 0 1em;
  }
  #order .step-3 h2 {
    margin-bottom: 1em;
  }
  #order .step-3 .more {
    grid-template-columns: repeat(auto-fill, 1fr);
    gap: 1em;
  }
}

form {
  background: hsla(230, 70%, 5%, 0.5);
  backdrop-filter: blur(5px);
  border: 1px solid hsla(111, 24%, 94%, 0.1);
  margin: 0 auto;
  border-radius: 1.75em;
  min-height: 375px;
  height: 100%;
  width: clamp(250px, 65%, 1000px);
  text-align: center;
  position: relative;
  display: grid;
  align-content: center;
}
form .buttons {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 1em;
}

.step-1 h2 {
  margin-bottom: 1em;
}
.step-1 .input-box {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.step-2 {
  height: max-content;
}
.step-2 .info {
  height: 250px;
  margin: auto 0;
}
.step-2 h2 {
  margin-bottom: 1em;
}
.step-2 .site-desc {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  gap: 0 3em;
  width: 50%;
  margin: 0 auto;
}
.step-2 .site-desc .inputs,
.step-2 .site-desc .check {
  margin-bottom: 1em;
}
.step-2 .site-desc .inputs input {
  margin: 1em 0;
}
.step-2 .site-desc .inputs input:placeholder-shown + label {
  top: 47%;
  left: 18px;
}
.step-2 .site-desc .inputs label {
  left: 16px;
}

.option {
  display: flex;
  width: max-content;
}

.step-3 h2 {
  margin-bottom: 1em;
}
.step-3 .more {
  display: grid;
  grid-template-columns: repeat(3, 165px);
  place-content: center;
  gap: 2.5em;
}
.step-3 .choice h3 {
  font-size: 1rem;
  text-align: initial;
  opacity: 0.8;
}

.step-4 {
  display: flex;
  justify-content: center;
  padding: 0 1em;
}
.step-4 textarea {
  width: clamp(275px, 50vw, 800px);
  height: 225px;
  margin: 1em auto 0;
  resize: none;
  font-family: "Poppins";
  font-weight: 300;
  letter-spacing: 1px;
  background: hsla(228, 43%, 9%, 0.5);
  backdrop-filter: blur(3px);
  border: 1px solid hsla(111, 24%, 94%, 0.1);
  border-radius: 1em;
  padding: 1em;
}
.step-4 textarea:focus {
  outline: none;
  border: 1px solid hsla(111, 24%, 94%, 0.3);
}

.step-5 h2 {
  margin-bottom: 8px;
}
.step-5 .radio {
  display: grid;
  grid-template-columns: repeat(3, 185px);
  justify-content: center;
  margin: 0 auto 2em;
}
.step-5 .radio > div {
  display: flex;
  gap: 4px;
}
.step-5 .radio input {
  opacity: 0;
}

.parent label {
  width: max-content;
  padding: 0 1em 0 1.75em;
  position: relative;
}
.parent label::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) scale(0.8);
  border-radius: 50%;
  border: 2px solid var(--yale-blue);
  left: 2px;
  width: 20px;
  aspect-ratio: 1/1;
  background: var(--mint-cream) url(/media/nav/check.svg) center/0px no-repeat;
  transition: 0.25s all ease-in-out;
}
.parent label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid var(--yale-blue);
  border-radius: 1em;
  width: 100%;
  height: 100%;
  transition: 0.25s all ease-in-out;
  opacity: 0;
}
.parent input:checked + label {
  color: var(--yale-blue);
}
.parent input:checked + label::after {
  background: var(--black-pearl) url(/media/nav/check.svg) center/14px no-repeat;
}
.parent input:checked + label::before {
  opacity: 1;
}

.confidentiality {
  width: max-content;
  margin: 0 auto;
}
.confidentiality > :first-child {
  background: hsla(228, 43%, 9%, 0.75);
  backdrop-filter: blur(3px);
  border: 1px solid hsla(111, 24%, 94%, 0.1);
  width: max-content;
  margin: 0 auto;
  padding: 6px 12px;
  border-radius: 1.75em;
  cursor: pointer;
}

#offer h1 {
  font-size: 1.4rem;
  text-align: center;
  width: 75%;
  margin: 1em auto 1.75em;
}
#offer h2 {
  opacity: 0.8;
  margin: 0 0 1em;
}
@media (max-width: 768px) {
  #offer {
    margin-top: 85px;
  }
  #offer h2 {
    font-size: 1.1rem;
