.offer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3em;
  justify-content: space-evenly;
  background: hsla(230, 70%, 5%, 0.5);
  backdrop-filter: blur(3px);
  border: 2px solid #3f3636;
  border-radius: 1.5em;
  padding: 1.75em;
}

.extras {
  text-align: center;
  grid-column: span 2;
}
.extras .list {
  grid-template-columns: 1fr 1fr 1fr;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1em;
  margin-top: 12px;
}

.product {
  border: 2px solid var(--oxford-blue);
  background: var(--black-pearl);
  backdrop-filter: blur(25%);
  border-radius: 1.5em;
  padding: 8px;
  outline: 1px solid hsla(0, 0%, 8%, 0.3);
  outline-offset: 3px;
  text-align: center;
}

.ord {
  text-align: center;
  width: max-content;
  margin: 3em auto;
}
.ord h1 {
  font-size: 1.2rem;
  margin: 0 auto 1.75em;
}
.ord a {
  background: var(--btn-grey);
  border: none;
  border-radius: 1em;
  padding: 1em 3em;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-shadow: var(--phthalo-blue) 0px 0px 3px, var(--powder-blue) 0px 0px 5px;
  box-shadow: -4px -4px 3px hsla(0, 0%, 20%, 0.521);
}

#works {
  min-height: calc(100vh - 86px);
}
#works h1 {
  margin: 1.5em 0 2em;
  text-align: center;
}
@media (max-width: 475px) {
  #works {
    min-height: calc(80vh - 86px);
  }
}

.site {
  position: relative;
  margin: 0 0 75px;
}

.responsive {
  position: relative;
  max-width: 800px;
  min-width: 300px;
  margin: 0 auto;
  height: auto;
}
.responsive img {
  width: 100%;
  height: 100%;
}
@media (max-width: 475px) {
  .responsive {
    margin: 25% auto 0;
  }
}

.descrip {
  display: flex;
  position: relative;
  width: max-content;
  margin: 1.75em auto 0;
  line-height: 20px;
  text-align: center;
}
.descrip h2 {
  margin: 0 0 8px;
  text-align: initial;
  width: max-content;
  max-width: none;
}
.descrip::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
  height: 4px;
  width: 75%;
  border-radius: 2px;
  background: hsla(223, 9%, 45%, 0.459);
}

.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  filter: invert(100%);
  opacity: 0.7;
  transition: 0.25s opacity ease-in-out;
  cursor: pointer;
  background: hsla(0, 0%, 100%, 0.375);
  width: 30px;
  border-radius: 50%;
  aspect-ratio: 1/1;
  box-shadow: #fff 0px 0px 10px, #fff 0px 0px 10px, #fff 0px 0px 10px;
}
.nav-btn img {
  width: 100%;
  height: 100%;
  scale: 1.1;
  position: relative;
  bottom: -3px;
}
.nav-btn:hover {
  opacity: 1;
}
.nav-btn.next {
  right: 0;
  transform: rotate(90deg);
}
.nav-btn.pre {
  left: 0;
  transform: rotate(-90deg);
}
@media (max-width: 475px) {
  .nav-btn {
    top: 80%;
  }
  .nav-btn.next {
    right: 3%;
    transform: rotate(90deg);
  }
  .nav-btn.pre {
    left: 3%;
    transform: rotate(-90deg);
  }
}

[active-site] > div {
  display: none;
}

[active-site="1"] > .site-1,
[active-site="2"] > .site-2 {
  display: initial;
}

.prev, .descrip {
  animation: siteIn 0.5s ease-out;
}

@keyframes siteIn {
  from {
    transform: translateX(25px);
  }
  to {
    transform: translateX(0);
  }
}
#pubs {
  min-height: calc(100vh - 86px);
  width: 100%;
  margin-bottom: 3em;
}
#pubs h1 {
  margin: 1.75em 0 3em;
  text-align: center;
}
@media (max-width: 640px) {
  #pubs {
    overflow-x: clip;
